import InsetLogin from '../../components/InsetLogin'
import request from '../../util/request'
import convertDate from '../../util/convertDate'

let currentDate = new Date()

export default {
    name: 'Bookcase',
    components: { InsetLogin },
    data () {
        return {
            modalName: '',
            listTouchStart: 0,
            listTouchDirection: null,
            // 假数据
            result: []
        }
    },
    computed: {
        userInfo() {
            return this.$store.state.userInfo;
        }
    },
    onLoad () {
        if (this.userInfo) {
            uni.startPullDownRefresh()
        }
    },
    onPullDownRefresh () {
        if (this.userInfo) {
            this.queryBookcase()
        } else {
            uni.stopPullDownRefresh()//得到数据后停止下拉刷新
        }
    },
    methods: {
        // ListTouch触摸开始
        touchStart (e) {
            this.listTouchStart = e.touches[0].pageX
        },
        // ListTouch计算方向
        touchMove (e) {
            this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left'
        },
        // ListTouch计算滚动
        touchEnd (e) {
            if (this.listTouchDirection === 'left') {
                this.modalName = e.currentTarget.dataset.target
            } else {
                this.modalName = null
            }
            this.listTouchDirection = null
        },
        navChange () {
            uni.switchTab({
                url: '/pages/navigation/Classify',
            })
        },
        getUserInfo (arg0) {
            this.userInfo = arg0
        },
        convertDate (updateTime) {
            return convertDate.convertZh(currentDate, updateTime)
        },
        queryBookcase () {
            let params = {
                condition: {
                    uniqueId: this.userInfo.uniqueId
                }
            }
            request.post('/relation/bookcase', params).then(data => {
                if (data.status === 200 && data.total > 0) {
                    this.result = data.data
                }
            }).finally(() => {
                uni.stopPullDownRefresh()//得到数据后停止下拉刷新
            })
        }
    }
}
